.ampstart-nav a,.ampstart-navbar-trigger,.ampstart-sidebar-faq a{text-decoration:none;cursor:pointer}
.ampstart-nav .ampstart-label{color:inherit;font-size: 1rem;}
.ampstart-navbar-trigger{font-size:2rem;line-height:3.5rem}
.ampstart-headerbar-nav{-ms-flex:1;flex:1 1}
.ampstart-nav-search{-ms-flex-positive:.5;flex-grow:.5}
.ampstart-headerbar .ampstart-nav-search:active,.ampstart-headerbar .ampstart-nav-search:focus,.ampstart-headerbar .ampstart-nav-search:hover{-webkit-box-shadow:none;box-shadow:none}
.ampstart-nav-search>input{border:none;border-radius:3px;line-height:normal}
.ampstart-nav-dropdown{min-width:200px}
.ampstart-nav-dropdown amp-accordion header{border:none;background-color:#fff}
.ampstart-nav-dropdown amp-accordion ul{background-color:#fff}
.ampstart-nav-dropdown .ampstart-dropdown-item,.ampstart-nav-dropdown .ampstart-dropdown>section>header{background-color:#fff;color:#000;font-size: 1rem;}
.ampstart-nav-dropdown .ampstart-dropdown-item a {font-size: 0.875rem;color: #000;}
.ampstart-nav-dropdown .ampstart-dropdown-item{color:#003f93}
.ampstart-sidebar{width:300px;min-width:300px;background-color:#fff;color:#000}
.ampstart-sidebar .ampstart-icon{fill:#003f93}
.ampstart-sidebar-header{min-height:3.5rem;line-height:3.5rem}
.ampstart-sidebar .ampstart-dropdown header,.ampstart-sidebar .ampstart-dropdown-item,.ampstart-sidebar .ampstart-faq-item,.ampstart-sidebar .ampstart-nav-item,.ampstart-sidebar .ampstart-social-follow{margin:0 0 2rem}
.ampstart-sidebar .ampstart-nav-dropdown{margin:0}
.ampstart-sidebar .ampstart-navbar-trigger{line-height:inherit}
.ampstart-navbar-trigger svg{pointer-events:none}
.ampstart-related-article-section{border-color:#4a4a4a}
.ampstart-related-article-section .ampstart-heading{color:#4a4a4a;font-weight:400}
.ampstart-related-article-readmore{color:#000;letter-spacing:0}
.ampstart-related-section-items>li{border-bottom:1px solid #4a4a4a}
.ampstart-related-section-items>li:last-child{border:none}
.ampstart-related-section-items .ampstart-image-with-caption{display:-ms-flexbox;display:flex;margin-bottom:0;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-align:center;align-items:center}
.ampstart-related-section-items .ampstart-image-with-caption>amp-img,.ampstart-related-section-items .ampstart-image-with-caption>figcaption{-ms-flex:1;flex:1 1}
.ampstart-related-section-items .ampstart-image-with-caption>figcaption{padding-left:1rem}
@media (min-width:40.06rem){.ampstart-related-section-items>li{border:none}
.ampstart-related-section-items .ampstart-image-with-caption>figcaption{padding:1rem 0}
.ampstart-related-section-items .ampstart-image-with-caption>amp-img,.ampstart-related-section-items .ampstart-image-with-caption>figcaption{-ms-flex-preferred-size:100%;flex-basis:100%}
}
.ampstart-social-box{display:-ms-flexbox;display:flex}
.ampstart-social-box>amp-social-share{background-color:#000}
.ampstart-icon{fill:#003f93}
.ampstart-input{width:100%}
main .ampstart-social-follow{margin-right:auto;margin-left:auto;width:315px}
main .ampstart-social-follow li{-webkit-transform:scale(1.8);-ms-transform:scale(1.8);transform:scale(1.8)}
h1+.ampstart-byline time{font-weight:400;font-size:1.5rem}
.ampstart-nav-dropdown .ampstart-dropdown-item {margin-bottom: 1rem;}
.ampstart-nav-dropdown amp-accordion ul {margin-bottom: 1rem;margin-top: -1rem;}


